import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FaEnvelope,
  FaExchangeAlt,
  FaLock,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";

import { ImTextColor } from "react-icons/im";
import { ImListNumbered } from "react-icons/im";

import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./DetailGroupEdit.css";

import { RoutesString } from "pages/routesString";
import DetailGroupAPI from "api/common/detail-group";
import SubjectAPI from "api/common/subject";
import GroupAPI from "api/common/group";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import { number } from "yup/lib/locale";
import i18n from "i18n/i18n";
import { isErrors } from "utils/utils";

export default function DetailGroupEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    idGN: null,
    idMH: null,
    ghiChu: "",
    // isDeleted: false,
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [gomNhom, setGomNhom] = useState([]);
  const [monHoc, setMonHoc] = useState([]);
  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    } else {
      setInitialValues({
        idGN: null,
        idMH: null,
        ghiChu: "",
        // isDeleted: false,
      });
    }
    try {
      const gomNhomRes = await GroupAPI.getAll({limit: -1});
      setGomNhom(gomNhomRes.contents);

      const monHocRes = await SubjectAPI.getAll({limit: -1});
      setMonHoc(monHocRes.contents);
    } catch (err) {
// eslint-disable-next-line
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await DetailGroupAPI.getById(id);
      setInitialValues({
        idGN: res.idGN !== undefined && res.idGN,
        idMH: res.idMH !== undefined && res.idMH,
        ghiChu: res.ghiChu,
        isDeleted: res.isDeleted,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values) {
    
    values.idGN = parseInt(values.idGN, 10);
    values.idMH = parseInt(values.idMH, 10);

    if (isUpdatePage) {
      const { isDeleted, ...body } = values;
      try {
        const res = await DetailGroupAPI.putById(id, body);
        if (res.status === 200) {
          toast.success(t("admin.UPDATE_SUCCESS"));
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const { isDeleted, ...body } = values;
      try {
        const res = await DetailGroupAPI.post(body);
        if (res.status === 201) {
          toast.success(t("admin.ADD_SUCCESS"));
          history.push(`${RoutesString.ADMIN_DETAIL_GROUP_EDIT}?id=${res.id}`);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await DetailGroupAPI.deleteById(id);
      if (res.status === 200) {
        setNotification({
          color: "success",
          isOpen: true,
          messages: [t("admin.common.DELETED")],
        });
        updateForm(id);
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.detail-group.UPDATE_DETAIL_GROUP")
            : t("admin.detail-group.ADD_DETAIL_GROUP")
        }
        items={[
          {
            href: RoutesString.ADMIN_DETAIL_GROUP_LIST,
            title: t("admin.detail-group.DETAIL_GROUP_LIST"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.detail-group.UPDATE_DETAIL_GROUP")
            : t("admin.detail-group.ADD_DETAIL_GROUP")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            idGN: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            idMH: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
          })}
        >
          {({ values, errors, dirty, isValid, handleChange, handleReset }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="idGN" className="col-lg-3">
                  {t("admin.detail-group.GROUP")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idGN"
                      value={values.idGN}
                      onChange={handleChange}
                    >
                      {gomNhom &&
                        gomNhom.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item?.loaiKhoiKienThuc?.khoiKienThuc?.chiTietNganh?.nganhDaoTao?.maNganhDaoTao}-${item?.loaiKhoiKienThuc?.khoiKienThuc?.chiTietNganh?.khoa}-${item?.loaiKhoiKienThuc?.khoiKienThuc?.ten}-${item?.loaiKhoiKienThuc?.ten}-${item?.loaiNhom}${item?.tieuDe ? `-${item?.tieuDe}` : ""}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="idMH" className="col-lg-3">
                  {t("admin.detail-group.SUBJECT")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idMH"
                      value={values.idMH}
                      onChange={handleChange}
                    >
                      {monHoc &&
                        monHoc.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.tenTiengViet}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="ghiChu" className="col-lg-3">
                  {t("admin.detail-group.NOTE")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <ImTextColor />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ghiChu"
                      value={values.ghiChu}
                      onChange={handleChange}
                      // invalid={errors.ghiChu}
                      // readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ghiChu && (
                    <FormText color="danger">{errors.ghiChu}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              {/* {isUpdatePage && (
                <>
                  <FormGroup className="row">
                    <Label for="isDeleted" className="col-lg-3">
                      {t("admin.common.AVAILABLE_STATUS")}
                    </Label>
                    <div className="col-lg-6">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaRemoveFormat />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="isDeleted"
                          value={values.isDeleted}
                          onChange={handleChange}
                        >
                          {availableStatus &&
                            availableStatus.map((item, index) => {
                              return (
                                <option key={index} value={item.value}>
                                  {item.name}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                </>
              )} */}
              <FormGroup>
                <Button
                  color="success"
                  disabled={isErrors(errors)}
                  onClick={() => handleEditOnClick(values)}
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
